:root {
    --background: 210 20% 98%;
    --foreground: 210 50% 10%;

    --card: 0 0% 100%;
    --card-foreground: 210 50% 10%;

    --popover: 0 0% 100%;
    --popover-foreground: 210 50% 10%;

    --primary: 210 100% 20%;
    --primary-foreground: 0 0% 100%;

    --secondary: 210 20% 96%;
    --secondary-foreground: 210 50% 10%;

    --muted: 210 20% 96%;
    --muted-foreground: 210 10% 45%;

    --accent: 24 100% 50%;
    --accent-foreground: 0 0% 100%;

    --destructive: 0 84% 60%;
    --destructive-foreground: 0 0% 100%;

    --success: 142 76% 36%;
    --success-foreground: 0 0% 100%;

    --warning: 38 92% 50%;
    --warning-foreground: 0 0% 100%;

    --border: 210 20% 90%;
    --input: 210 20% 90%;
    --ring: 210 100% 20%;

    --radius: 0.75rem;

    /* Brand Colors */
    --brand-blue: 210 100% 20%;
    --brand-blue-light: 210 60% 95%;
    --brand-blue-medium: 210 80% 40%;
    --brand-orange: 24 100% 50%;
    --brand-orange-light: 24 100% 95%;

    /* Gradients */
    --gradient-hero: linear-gradient(135deg, hsl(210 60% 97%) 0%, hsl(210 80% 92%) 50%, hsl(210 60% 97%) 100%);
    --gradient-card: linear-gradient(180deg, hsl(0 0% 100%) 0%, hsl(210 20% 99%) 100%);
    --gradient-sidebar: linear-gradient(180deg, hsl(210 100% 18%) 0%, hsl(210 100% 15%) 100%);

    /* Shadows */
    --shadow-card: 0 4px 6px -1px hsl(210 100% 20% / 0.05), 0 2px 4px -2px hsl(210 100% 20% / 0.05);
    --shadow-card-hover: 0 20px 25px -5px hsl(210 100% 20% / 0.1), 0 8px 10px -6px hsl(210 100% 20% / 0.1);
    --shadow-button: 0 4px 14px 0 hsl(24 100% 50% / 0.35);

    --sidebar-background: 210 100% 18%;
    --sidebar-foreground: 210 20% 95%;
    --sidebar-primary: 24 100% 50%;
    --sidebar-primary-foreground: 0 0% 100%;
    --sidebar-accent: 210 100% 25%;
    --sidebar-accent-foreground: 0 0% 100%;
    --sidebar-border: 210 100% 25%;
    --sidebar-ring: 24 100% 50%;
  }

*{
  border-color: hsl(var(--border));
}

body{
  background-color: hsl(var(--background));
  font-family: "Plus Jakarta Sans", system-ui, sans-serif;
  color: hsl(var(--foreground));
